.head {
  width: max-content;
  margin: 0 auto 40px auto;
  max-width: 500px;
  text-align: center;
  font-size: 18px;
}

.main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.mainShort {
  display: flex;
  flex-direction: column;
  gap: 20px;
  text-align: center;
}
.mainChildren {
  position: relative;
  padding: 25px 5px;
  max-width: 300px;
  border: 1px solid #00457c;
  border-radius: 10px;
  text-align: center;
  cursor: pointer;
  transition: 0.5s;
}
.mainChildrenText {
  border-bottom: 1px solid #00457c;
  padding: 0;
  padding-bottom: 10px;
  padding-left: 20px;
  max-width: 100%;
  display: flex;
  justify-content: flex-start;
}

.mainChildren:hover {
  background: white;
  border: 1px solid white;
  color: black;
}
.clouse {
  position: absolute;
  top: 5%;
  right: 5%;
  transition: 0.5s;
  color: white;
}
.clouse:hover {
  transform: scale(1.1, 1.1);
  text-shadow: 0 0 5px white;
  color: black;
  font-weight: bold;
  transition: 0.5s;
}
@media (max-width: 600px) {
  .head {
    max-width: 250px;
  }
  .main {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 550px) {
  .mainChildren:hover {
    background: transparent;
    border: 1px solid #00457c;
    color: white;
  }
}
