.main {
  background-color: var(--background-container);
  padding: 20px 50px;
  border-radius: 10px;
}
.main h2 {
  text-align: center;
  margin-bottom: 30px;
}
.main p {
  max-width: 500px;
}
.main ul {
  margin: 10px 0 20px 20px;
}
.main button {
  margin-top: 10vh;
  border: none;
  font-size: 18px;
  padding: 5px 15px;
  background-color: var(--paginator-border);
  color: white;
  font-weight: 700;
  border-radius: 10px;
  cursor: pointer;
}

@media (max-width:700px){
    .main p {
        max-width: 300px;
      }
}
